<template>
  <div class="mt-3 col users_box">
    <div class="box">
      <div @click="pickUser(user, $event)" class="user"
      v-for="user in GET_SEARCHED_USERS" :key="user.id"> 
          <span class="user_name">{{user.username}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters,mapActions, mapMutations} from 'vuex';
export default {
  name: "VUsers",
  computed: mapGetters(["GET_IS_FETCHING", "GET_SEARCHED_USERS"]),
  methods: {
    ...mapActions(["FETCH_USERS"]),
    ...mapMutations(["UPDATE_USERS", "UPDATE_IS_FETCHING", "SET_RECEIVER_ID"]),
    pickUser(user, $event){
      if ($event.target.classList.contains('active') || $event.target.parentNode.classList.contains('active') ) {
        $event.target.classList.remove('active');
        $event.target.parentNode.classList.remove('active');
        return;
      }
      const users = document.querySelector('.box');
      Object.entries(users.children).forEach(u => u[1].classList.remove('active'));
      $event.target.classList.contains('user')
        ? $event.target.classList.add('active')
        : $event.target.parentNode.classList.add('active');
      this.SET_RECEIVER_ID(user._id);
    }
  },
};
</script>

<style lang="scss" scope>
.box{
    display: flex;
    flex-direction: column;
    padding: 10px 0;
}
.users_box{
    // display: flex;
    width: 100%;
    border: 1px solid #000;
    overflow: auto;
    height: 47vh;
    border-radius: 6px;
}
.user_name{
    padding: 0 10px;
}
.user{
    // padding: 0 10px;
    border-radius: 10px;
    height: 50px;
    display: flex;
    align-items: center;
    width: 100%;
    
    span {
      font-weight: 400;
    }
}
.user:hover{
    cursor: pointer;
    background: rgba(126, 14, 136, 0.671);
    color: aliceblue;
}
.active{
  background: rgb(80, 4, 116, .2);
}
</style>