import { createStore } from 'vuex';
import router from '../router';
import {showNotification} from "../components/functions/js-notification";


const Storage = window.localStorage;

export default createStore({
  state: {
    isFetching: false,
    users: [],
    searchChars: "",
    searchedUsers: [],
    currentUser: {},
    isUserLoggedin: false,
    receiverId: null,
    vapidKey: "BJ47mTyLGqEnQZR0kymxJmReHKjE9jJgFEPnwbHzYR8RKi3dUguQZvmFu4a49oWkn7JvYRQYSvcQhpidpVlbWVI"
  },
  mutations: {
    UPDATE_IS_FETCHING(state, value){
      state.isFetching = value;
    },
    UPDATE_USERS(state, users){
      state.users = users;
    },
    UPDATE_SEARCHED_USERS(state, value){
      state.searchChars = value;
    },
    SET_USER(state, user){
      // state.currentUser.token = user.token;
      // state.currentUser.username = user.username;
      // state.currentUser.id = user.id;
      Storage.setItem('token', user.token);
      Storage.setItem('username', user.username);
      Storage.setItem('id', user.id);
      Storage.setItem('isLoggedIn', true);
      state.isUserLoggedin = true;
      router.push({name: "main"});
      location.reload();
    },
    SET_RECEIVER_ID(state, id){
      // state.receiverId = id;
      Storage.receiverId = id;
    },
    LOGOUT(state){
      // state.currentUser.token = null;
      // state.currentUser.username = null;
      // state.currentUser.id = null;
      Storage.removeItem('token');
      Storage.removeItem('username');
      Storage.removeItem('id');
      Storage.removeItem('isLoggedIn');
      state.isUserLoggedin = false;
      location.reload();
    }
  },
  actions: {
    async FETCH_USERS(ctx){
      try{
        const res = await fetch(`https://web-notifications.ru/users`, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${window.localStorage.token}`
          },
          credentials: 'include',
          mode: 'cors',
        });
        console.log(res.status);
        if (res.status == 200) {
          const data =  await res.json();
          ctx.commit('UPDATE_USERS', data.users);
          return;
        }
        // if (res.status == 403) {
        //   // ctx.commit('LOGOUT');
        //   router.push({name: "login"});
        //   return;
        // }
        console.log('An error occured during authorisation');
      }
      catch (err) {
        console.log(err);
      }
    },
    async FETCH_LOGIN(ctx, user){
      try{
        const username = user.username;
        const password = user.password;
        const url = 'https://web-notifications.ru/login';
        const res = await fetch(url, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        mode: 'cors',
        body: JSON.stringify({username, password})
    });
    const response = await res.json();
    if (response.status == 200) {
      ctx.commit("SET_USER", response.user);
    } else {
        console.log('something wrong')
    }
      }
      catch(err){
        console.log(err);
      }
    },
    async FETCH_REGISTRATION(ctx, user) {
      const username = user.username;
      const password = user.password;
      const url = "https://web-notifications.ru/registration";
      const res = await fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include",
        mode: "cors",
        body: JSON.stringify({ username, password }),
      });
      const response = await res.json();
      if (response.status == 201) {
        window.location.href = "/login";
      } else {
        console.log(response);
      }
    },
    async FETCH_SUBSCRIBE(ctx){
      /**
       * check scope in prod mode
       * @type {ServiceWorkerRegistration}
       */
       const registration = await navigator.serviceWorker.register(
         "service-worker.js",
         { scope: "/static/service-worker.js" }
       );
       console.log("registration   --> ", registration);
       const subscription = await registration.pushManager.subscribe({
         userVisibleOnly: true,
         applicationServerKey: this.urlBase64ToUint8Array(this.GET_VAPID_KEY),
       });
       const payload = {
         userId: Storage.id,
         subscription,
       };
       console.log("subscription   --> ", subscription);
       await fetch("https://web-notifications.ru/subscribe/", {
         method: "POST",
         headers: {
           "Content-Type": "application/json",
           'Authorization': `Bearer ${Storage.token}`
         },
         credentials: "include",
         mode: "cors",
         body: JSON.stringify(payload),
       });
    },
    async SEND_MESSAGE(ctx, msg){
      const title = msg.title;
      const body = msg.body;
      const id = Storage.receiverId;
      const payload = {id, title, body}
      try{
        const res = await fetch('https://web-notifications.ru/sent-notifications', {
          method: 'POST',
          credentials: 'include',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${window.localStorage.token}`
          },
          body: JSON.stringify(payload)
      });
      const data = await res.json();
      console.log(data);
      if (res.status === 200) {
        msg.callback();
        showNotification('Message successfully sent', ...[,,], 3000)
      }
      }
      catch (err){
        console.log(err);
        alert('Ooops, there is an error. Might be the user can not get notifications at the moment.')
      }
    }
  },
  getters: {
    GET_IS_FETCHING(state){
      return state.isFetching;
    },
    GET_USERS(state){
      return state.users;
    },
    GET_SEARCHED_USERS(state){
      if (!state.searchChars.length) return state.users;
      state.searchedUsers = [];
      for (let i = 0; i < Object.entries(state.users).length; i++) {
        const user = Object.entries(state.users)[i][1];
        if (user.username.toLowerCase().includes(state.searchChars.toLowerCase())) {
          state.searchedUsers.push(user);
        }
      }
      // for (let i = 0; i < state.users.length; i++) {
      //   const user = state.users[i];
      //   if (user.name.toLowerCase().includes(state.searchChars.toLowerCase())) {
      //     state.searchedUsers.push(user);
      //   }
      // }
      return state.searchedUsers;
    },
    GET_IS_LOGGED_IN(state){
      // return state.isUserLoggedin;
      return [state.isUserLoggedin, Storage.isLoggedIn];
    },
    GET_RECEIVER_ID(state){
      // return state.receiverId;
      return Storage.receiverId;
    },
    GET_VAPID_KEY(state){
      return state.vapidKey;
    }
  }
});
