<template>
  <div
    class="container col-6 mt-5 pt-5 bg-light"
    style="border-radius: 8px; min-width: 400px"
  >
    <h5 class="text-center">Please login</h5>
    <div class="container col-8 text-left mt-5">
      <div class="mb-3">
        <label for="loginInput" class="form-label">Login</label>
        <input v-model="username" type="email" class="form-control" id="loginInput" />
      </div>
      <div class="mb-3">
        <label for="passwordInput" class="form-label">Password</label>
        <input
          v-model="password"
          class="form-control"
          type="password"
          id="passwordInput"
          rows="3"
        />
      </div>
      <div class="mb-3">
        <button @click="login" class="btn btn-success loginBtn mb-5">
          Send
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations} from 'vuex';

export default {
  name: "login",
  methods: {
    ...mapActions(["FETCH_LOGIN"]),
    ...mapMutations(["UPDATE_USERS", "UPDATE_IS_FETCHING"]),
    login(){
        const username = this.username;
        const password = this.password;
        if(this.username && this.password) this.FETCH_LOGIN({username, password})
        else{
            console.log('Input username && pasword');
        }
    }
  },
  data(){
      return{
          username: "",
          password: ""
      }
  },
};
</script>

