<template>
  <div class="row">
    <div class="col">
      <button @click="subscribeFunc" class="btn btn-primary sbscb">
        Subscribe!
      </button>
    </div>
    <div class="container mt-5">
      <p class="h5">Send push-notifications:</p>
      <div class="mb-3">
        <label for="msg_title" class="form-label">Title:</label>
        <input
          v-model="title"
          type="text"
          class="form-control bg-light"
          id="msg_title"
        />
      </div>
      <div class="mb-3">
        <label for="msg_body" class="form-label">Message body:</label>
        <input
          v-model="body"
          class="form-control bg-light"
          type="text"
          id="msg_body"
        />
      </div>
      <button
        class="btn btn-success btn-large mt-3 sendbtn"
        id="send"
        @click="sendMessage"
      >
        Send!
      </button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";

export default {
  computed: mapGetters(["GET_VAPID_KEY"]),
  methods: {
    ...mapActions(["SEND_MESSAGE", "FETCH_SUBSCRIBE"]),
    ...mapMutations(["UPDATE_USERS", "UPDATE_IS_FETCHING", "SET_RECEIVER_ID"]),
    sendMessage() {
      const callback = () => {
        this.title = "";
        this.body = "";
      };
      const title = this.title;
      const body = this.body;
      this.SEND_MESSAGE({ title, body, callback });
    },
    urlBase64ToUint8Array(base64String) {
      const padding = "=".repeat((4 - (base64String.length % 4)) % 4);
      const base64 = (base64String + padding)
        .replace(/\-/g, "+")
        .replace(/_/g, "/");

      const rawData = window.atob(base64);
      const outputArray = new Uint8Array(rawData.length);

      for (let i = 0; i < rawData.length; ++i) {
        outputArray[i] = rawData.charCodeAt(i);
      }
      return outputArray;
    },
    async subscribeFunc() {
      this.FETCH_SUBSCRIBE();
    },
  },
  data() {
    return {
      title: "",
      body: "",
    };
  },
  mounted() {
    this.urlBase64ToUint8Array("kjsdhfkjshk");
  },
};
</script>

